import { RECEIVE_ENTITIES } from '../constants/actionTyeps'

const initialState = {
    articles: {},
}



export default function entities(state = initialState, action) {
    switch (action.type) {
        case RECEIVE_ENTITIES:
            return Object.assign(state, action.entities)
        default:
            return state
    }
}


